* {
  margin: 0;
  font-family: 'Roboto', 'Open Sans', 'Noto Sans JP', sans-serif;
  transition: 0.2s;
  text-decoration: none !important;
}

.button-primary {
  padding: 12px;
  border-radius: 8px;
  border: 1.5px solid #202020;
  background-color: #00cc99;
  cursor: pointer;
  color: #202020;
  font-weight: bolder;
  font-size: 16px;

  &.large {
    border-radius: 12px;
  }
  &:hover {
    background-color: #00f2b7;
  }
}

/* ANIMATIONS */

.rotate-right {
  transform: rotate(90deg);
}

.spin-right-infinite {
  animation: spin 1s infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.scroll-arrow {
  position: absolute;
  left: 50%;
  top: 90%;
  height: auto;
  margin: 0 auto;
  color: rgb(224, 224, 224);
  animation: shake 2.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) infinite;
}

@keyframes shake {
  10%,
  30% {
    transform: translate3d(0, -1px, 0);
  }

  20%,
  40% {
    transform: translate3d(0, 1px, 0);
  }

  50%,
  60%,
  70%,
  80%,
  90%,
  100% {
    transform: translate3d(0, 0, 0);
  }
}

.animations-div {
  height: 150px;
  width: 100%;
}

@media screen and (max-width: 580px) {
  .image-container img {
    height: 100vh;
  }
}

